<template>
  <div id="tablet">
    <div class="queryBox">
      <QueryBox :formArr="queryFrom" @query="getQueryData" style="width: calc(100% - 150px)"></QueryBox>
      <div class="queryBtns">
        <Button type="success" size="small" style="margin-right: 10px" class="btn-add" icon="md-add" @click.stop="openAddModal"></Button>
        <Button type="success" size="small" class="btn-import" style="padding: 2px 7px 1.8px">
          <Upload
            :show-upload-list="false"
            :format="['.xls']"
            action="#">
            <i class="iconfont icon-upload"></i>
            <span></span>
          </Upload>
        </Button>
      </div>
    </div>
    <div class="content_box">
      <div class="table-box">
        <Table border stripe :loading="loading" :columns="columns1" :data="data1" highlight-row :height="tableHeight"></Table>
      </div>
      <div class="page_box">
        <Page :total="total" :page-size="pageSize" :current.sync="pageNum" @on-change="pageChange" show-total/>
      </div>
    </div>
    <div class="extract_box" :class="{addActive:addActive}">
      <extracTableMap ref="extracTableMap" @signOut="signOut"></extracTableMap>
    </div>
    <Modal v-model="modal1"
           title=""
           :mask-closable="false">
      <Form :model="addForm" :label-width="100" ref="addForm" :rules="addFormRules">
        <FormItem prop="name" label="">
          <Input v-model="addForm.name" placeholder=""></Input>
        </FormItem>
        <FormItem prop="url" label="">
          <Input v-model="addForm.url" placeholder=""></Input>
        </FormItem>
        <FormItem label="">
          <Input type="textarea" v-model="addForm.remark" placeholder=""></Input>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button @click="modal1=false"></Button>
        <Button type="primary" @click="submitAddForm" :loading="buttonLoading"></Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import { getRemoteList, addRemoteTask, deleteRemoteTask } from '@/api/remoteSensing'
import QueryBox from '@/components/publicQuery'
import extracTableMap from './extracTable_map'
/*  */
const ADDFORMRULES = {
  name: [
    { required: true, message: '', trigger: 'blur' },
    { type: 'string', min: 4, message: '4-20', trigger: 'blur' },
    { type: 'string', max: 20, message: '4-20', trigger: 'blur' }
  ],
  url: [
    { required: true, message: '', trigger: 'blur' }
  ]
}
export default {
  name: 'extracTable',
  data () {
    return {
      /*  */
      queryData: {},
      /*  */
      tableHeight: Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40,
      /*  */
      columns1: [
        {
          title: '',
          key: 'index',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + (this.pageNum - 1) * this.pageSize + 1)
          }
        },
        {
          title: '',
          align: 'center',
          minWidth: 200,
          key: 'name'
        },
        {
          title: '',
          align: 'center',
          minWidth: 250,
          key: 'remark'
        },
        {
          title: '',
          align: 'center',
          width: 300,
          key: 'url'
        },
        {
          title: '',
          key: 'address',
          width: 240,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'warning',
                  size: 'small',
                  icon: 'md-create'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.activeId = params.row.id
                    this.openMap()
                  }
                }
              }, ''),
              h('Button', {
                props: {
                  type: 'error',
                  size: 'small',
                  icon: 'md-trash'
                },
                on: {
                  click: () => {
                    this.$Modal.confirm({
                      title: '',
                      content: '',
                      onOk: () => {
                        this.proDelete(params.row.id)
                      },
                      onCancel: () => {
                      }
                    })
                  }
                }
              }, '')
            ])
          }
        }
      ],
      data1: [],
      /*  */
      obj: {},
      /*  */
      total: 0,
      pageNum: 1,
      pageSize: Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48),
      /*  */
      loading: false,
      /*  */
      addActive: false,
      /*  */
      addForm: {
        name: '',
        url: '',
        remark: ''
      },
      /*  */
      addFormRules: ADDFORMRULES,
      /*  */
      modal1: false,
      /* id */
      activeId: 0,
      /*  */
      buttonLoading: false
    }
  },
  components: {
    QueryBox,
    // 
    extracTableMap
  },
  computed: {
    // 
    queryFrom: function () {
      return [
        {
          type: 1,
          field: 'name',
          title: ''
        }
      ]
    }
  },
  created () {
    this.getTableList()
  },

  mounted () {
    // 
    window.addEventListener('resize', () => {
      this.tableHeight = Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40
      this.pageSize = Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48)

      // this.tableHeight = window.innerHeight - 312
      // this.pageSize = Math.floor((window.innerHeight - 312) / 48)
      this.getTableList()
    }, false)
  },
  watch: {
    queryData () {
      this.getTableList()
    }
  },
  methods: {
    /*  */
    getQueryData (data) {
      this.queryData = data
      this.pageNum = 1
      this.getTableList()
    },
    /*  */
    getTableList () {
      this.loading = true
      getRemoteList({
        data: {
          pageSize: this.pageSize,
          pageNum: this.pageNum,
          ...this.queryData
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.data1 = res.data.data.rows
          this.total = res.data.data.total
          this.loading = false
        }
      }).catch(() => {
        this.$Notice.error({ title: '' })
      })
    },
    /*  */
    pageChange (pageNum) {
      this.pageNum = pageNum
      this.getTableList()
    },
    /*  */
    signOut (addActive) {
      this.addActive = addActive
    },
    /*  */
    openAddModal () {
      this.modal1 = true
      this.$refs.addForm.resetFields()
    },
    /*  */
    openMap () {
      this.addActive = true
      this.$refs.extracTableMap.initCom()
    },
    /*  */
    submitAddForm () {
      // 
      this.buttonLoading = true
      // 
      addRemoteTask({
        data: {
          name: this.addForm.name,
          url: this.addForm.url,
          remark: this.addForm.remark,
          // todo 
          createBy: 1 // id
        }
      }).then(res => {
        // 
        this.buttonLoading = false
        // 
        if (res.data.code === '0000') {
          //  
          this.getTableList()
          // 
          this.$Notice.success({ title: '' })
          this.modal1 = false
        } else {
          // 
          this.$Notice.error({
            title: '',
            desc: res.data.msg
          })
        }
      })
    },
    /*  */
    proDelete (id) {
      deleteRemoteTask({
        data: {
          id: [id]
        }
      }).then(res => {
        if (res.data.code === '0000') {
          this.getTableList()
          this.$Notice.success({ title: '' })
        } else {
          this.$Notice.error({ title: '', desc: res.data.msg })
        }
      })
    }
  }
}
</script>

<style lang='scss' scoped rel='stylesheet/scss'>
  #tablet{
    position: relative;
    .iconfont {
      margin-right: 4px;
      &.icon-xuanzewenjian {
        font-size: 12px;
      }
      &.icon-upload {
        font-size: 12px;
      }
    }
    .queryBox {
      height: 50px;
      border-bottom: 1px solid #d3d3d3;
      margin: 0 15px 15px;
      position: relative;
      overflow: inherit;
      .queryBtns {
        float: right;
        margin-top: 14px;
      }
    }
    .content_box {
      margin: 0 15px 0;
      height: calc(100% - 65px);
      .page_box{
        width: 100%;
        padding: 15px;
        text-align: right
      }
      .table-box {
        height: calc(100% - 60px);
      }
    }
    .extract_box{
      height: 100%;
      width: 100%;
      position: absolute;
      right: -120%;
      top:0;
      z-index: 1356;
      background: #fff;
      transition: all 0.8s ease;
      border-radius: 0 0 5px 5px;
      .closeBtn{
        position: absolute;
        right:10px;
        bottom:10px;
        z-index: 131531313;
      }
    }
    .addActive{
      right:0;
    }
    .btn-import {
      background-color: rgb(194,146,1);
      border-color: rgb(194,146,1);
      &:hover, &:focus {
        background-color: #B28301;
        border-color: #B28301;
      }
    }
    .btn-add {
      background-color: #01c296;
      border-color: #01c296;
      &:hover, &:focus {
        background-color: #05ab85;
        border-color: #05ab85;
      }
    }
  }
</style>
